import FeatherIcon from 'feather-icons-react';
import '../css/Navbar.css'
import {Link} from 'react-router-dom'
function Navbar() {
    return(
        <div className="navbar">
            <Link to="/">
            <FeatherIcon icon="tv" className="logo" />
            </Link>
            <ul className="navbar-nav">
                <Link to="/search">
                <li className="nav-items"><FeatherIcon icon="search" /></li>
                </Link>
                <li className="nav-items"><FeatherIcon icon="github" /></li>
            </ul>
            
        </div>
    );
}

export default Navbar;