import '../css/Home.css'
import React, { useState, useEffect } from 'react'
import PosterFetcher from '../components/PosterFetcher';
import FeatherIcon from 'feather-icons-react';

function Home() {

    useEffect(() => {
        fetchdata();
    },[]);

    const [items, setitems] = useState([]);
    const [isloaded, setisloaded] = useState(false);
    
    const fetchdata = async () => {

        const data = await fetch('https://gogobeel.herokuapp.com/NewSeasons/1');
        const res = await data.json();
        console.log(res);
        setitems(res);
        setisloaded(true);
    }

    localStorage.setItem("animeData",JSON.stringify(items))
    
    return(<>
        {
            isloaded?
            <div className="home-wrapper">

            <div className="home-banner">
            <img src={items[0].img_url}/>
            </div>

            <PosterFetcher lable="Popular"/>

            <PosterFetcher lable="NewSeasons"/>

        </div>
        :<div className="loading-wrapper">
        <ul>
        <li><FeatherIcon icon="refresh-cw" /></li>
        <li><p>Loading..</p></li>
        </ul>
        </div>
            
        }
        
        </>
    );
}

export default Home;