import React, { useState, useEffect } from 'react'

function Play({match}) {

    useEffect(() => {
        fetchdata();
    },[]);

    const [items, setitems] = useState([]);
    const [isloaded, setisloaded] = useState(false);
    const [src, setsrc] = useState()
    const [quality, setquality] = useState()

    const fetchdata = async () => {

        console.log(match.params.id)
        const data = await fetch(`http://gogobeel.herokuapp.com/getEpisode/${match.params.id}`);
        const res = await data.json();
        console.log(res);
        setitems(res);
        setsrc(res[0].ep_link);
        setquality(res[0].quality);
        setisloaded(true);

    }

    return(
        <>
        {
            isloaded?
            <div className="play-wrapper">
            <video controls style={{width:"100%"}} src={src} />
            <p style={{color:"white", textAlign:"center"}}>current quality : {quality} </p>
            <p className="episode-break">Links</p>
            {
                            items.map(item=>
                                (   
                                    
                                    item.quality.startsWith("w")?
                                    <div className="episode-wrapper"  onClick={()=>{setsrc(item.ep_link); setquality(item.quality)}}>
                                    <p>{item.quality}</p>
                                    </div>
                                    :<></>
                                    
                                )
                                )
                }
            </div>
            
            :<p>Loading</p>
        }
        </>
    );
}
export default Play;