import '../css/PosterFetcher.css'
import React, { useState, useEffect } from 'react'
import { Link } from 'react-router-dom';

function PosterFetcher(props) {

    useEffect(() => {
        fetchdata();
    },[]);

    const [items, setitems] = useState([]);
    const [isloaded, setisloaded] = useState(false);
    const fetchdata = async () => {

        const data = await fetch(`https://gogobeel.herokuapp.com/${props.lable}/1`);
        const res = await data.json();
        console.log(res);
        setitems(res);
        setisloaded(true);
    }

    return(
        <div className="category-card">
                <p>{props.lable}</p>
                        <div className="scroll-view-wrapper">{
                            items.map(item=>
                                (   
                                    <Link to={`Anime/${item.anime_id}`}>
                                    <div className="poster-banner">
                                    <img src={item.img_url}></img>
                                    <p>{item.name}</p>
                                    </div>
                                    </Link>
                                )
                                )
                        }
                   </div> 
            </div>
    );
}

export default PosterFetcher;

