import React ,{ useState } from 'react'
import FeatherIcon from 'feather-icons-react';
import {Link} from 'react-router-dom'
import '../css/Search.css'
function Search(params) {


    const [items, setitems] = useState([]);
    const [query, setquery] = useState();
    
    const fetchdata = async (val) => {

        setquery(val.target.value);
        const data = await fetch(`http://gogobeel.herokuapp.com/search/${val.target.value}`);
        const res = await data.json();
        console.log(res);
        setitems(res);

    }

    return(
        <div>
            <div className="search-bar">
            <FeatherIcon icon="search" className="search-icon"/>
               < input type="text" name="search-input" value={query} className="search-input" onChange={fetchdata} placeholder="Search"/>
            </div>
            <div className="search-result-wrapper">
            {
                            items.map(item=>
                                (   
                                    <Link to={`Anime/${item.anime_id}`}>
                                    <div className="poster-banner">
                                    <img src={item.img_url}></img>
                                    <p>{item.name}</p>
                                    </div>
                                    </Link>
                                )
                                )
            }</div>
        </div>
    );
}

export default Search;